@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  h1 {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    height: 100px;
  }
  
  nav {
    background-color: black;
    overflow: hidden;
  }
  
  nav a{
  float: center;
  display: block;
  color: white;
  text-align: center;
  padding: 14px 20px;
  text-decoration: none;
  
  }
  
  nav ul{
    list-style-type: none;
  }
  
  body {
    background-color: black;
  }
  
  

  @media (min-width: 768px){
    Accordion{
      width: 100%;
    }
  }